
.selectedPage {
    font-weight: bold;
    font-size: 20px;
    cursor: default;
    padding-right: 5px;

}
.pageNumbers {
    display: flex;
    overflow: auto;
    overflow-y: hidden;
}
.pageNumbers::-webkit-scrollbar {
    width: 0.2em;
}

.pageNumbers::-webkit-scrollbar-thumb {
    background-color: springgreen;
    outline: 1px solid red;
}
.pageNumber{
    cursor: default;
    font-size: 20px;
    padding-right: 5px;

}
.pageNumber:hover {
    cursor: pointer !important;


}
.selectedPage:hover {
    cursor: pointer;
}

.h1 {
    display: inline-block;
    background: springgreen;
    height: 80px;
    margin-top: 0px;
    width: 740px;
    line-height: 80px;
    padding-left: 60px;
    font-family: "Comic Sans MS";
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 20px;
}
